@media only screen and (max-width:1248px){
    .first-sec img{
        width: 400px;
    }

    .sub-first{
        margin-top: 8%;
    }
    .second-sec img{
        width: 400px;
    }

    .sub-second{
        margin-top: 8%;
    }
    .fourth-sec img{
        width: 400px;
    }

    .sub-fourth{
        margin-top: 8%;
    }

    .faq h1{
        font-size: 35px;
        margin-left: 2%;
    }

    .question{
        padding: 10px 20px;
        margin-right: 14%;
    }

    .faq-input{
        margin-left: 10%;
    }
}

@media only screen and (max-width: 1108px){
    .faq h1{
        margin-left: 1%;
    }

    .question{
        margin-right: 12%;
    }

    .form input{
        width: 45%;
    }
}

@media only screen and (max-width: 912px){
    .form button{
        display: block;
        margin-top: 3%;
        margin-left: 37%;
        padding: 15px 24px;

    }

    .form input{
        width: 75%;
        padding: 13px;
    }

    .form label{
        font-size: larger;
    }

    .first-sec{
        display: block;
    }
    .first-sec img{
        margin-left: 18%;
    }
    
    .sub-first{
        width: 630px;
    }
    
    .sub-first h2{
        margin-left: 25%;
        padding: 1%;
    }

    .sub-first h5{
        font-size: 20px;
        text-align: center;
    }
    .second-sec{
        display: block;
        transform: rotate(180deg);
        border: none;
        border-top: 8px #222 solid;
    }
    .second-sec img{
        /* transform: rotate(180deg); */
        margin-left: 18%;
        width: 65%;
    }
    
    .sub-second{
        /* transform: rotate(180deg); */
        width: 630px;
        
    }
    
    .sub-second h2{
        margin-top: 5%;
        text-align: center;
        padding: 1%;
        width: max-content;
        font-size: larger;
        font-family: Verdana, Geneva, Tahoma, sans-serif;
    }

    .sub-second h5{
        font-size: 20px;
        text-align: center;
    }

    .faq{
        margin-left: 10%;
    }

    .faq-input{
        margin-left: 2%;
    }
}